        font-size: 0.7rem;
        font-family: "";
        color: #676767;
        & > div {
          overflow: hidden;
          & > div {
            float: left;
            width: 50%;
            text-align: left;
            span {
              margin-left: 0.2rem;
            }
          }
          & .left {
            & > span:nth-of-type(1) {
              margin-left: 0;
            }
            & .price {
              margin-left: 0.8rem;
              color: #a6a6b0;
            }
          }
          & .right {
            padding-left: 0.2rem;
            & .percent {
              margin-left: 0.8rem;
            }
            &.rise .percent {
              margin-left: 0.6rem;
            }
            &.fall {
              color: #ff0000;
            }
            &.rise {
              color: #19b369;
            }
          }
          & .right > span:nth-of-type(1) {
            margin-left: 0;
          }
        }
        & > div:last-child {
          margin-top: 0.1rem;
        }
      }
    }
    .hide {
      display: none;
    }
    .btcToTop {
      position: fixed;
      bottom: 5rem;
      right: 2rem;
      z-index: 666;
      width: 50px;
      height: 50px;
      line-height: 50px;
      text-align: center;
      background-color: #11b4c3;
      color: white;
    }
  }

